import React, { Component } from 'react'
import styled from 'styled-components'

const InputField = styled.input`
    text-align:center;
    margin-bottom:2em;
    width:30em;
`

const Flex = styled.div`
    display:flex;
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
`

const ColorBox = styled.span`
    display:flex;
    width:10px;
    height:10px;
    background-color:${props => props.color || "white"};;
    border: 1px solid black;
    margin-left:5px;`

export default class SouvenirForm extends Component {

    state={
        name:"",
        location:"",
        color:"blue",
    }

    pickColor = (e) => {
        this.setState({
            color:e.value
        })
    } 
  render() {
    return (
      <Flex>
        <InputField type="text" placeholder="Name"/>
        <InputField type="text" placeholder="Location"/>
        <InputField type="text" placeholder="Add song link"/>
        <Flex direction="row">
        <select onChange={this.pickColor(this.value)}>
            <option selected value="red">Red</option>
            <option value="blue">Blue</option>
            <option value="mercedes">Green</option>
            <option value="audi">Black</option>
        </select>
        <ColorBox color={this.state.color}/>
        </Flex>

      </Flex>
    )
  }
}
